*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.custom-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: absolute;
  top: 38rem;
  width: 100%;
  font-family: "Exo 2";
}

@media (max-width: 768px) {
  .custom-nav {
    display: none;
  }
}

.nav-btn {
  text-decoration: none;
  color: rgb(24, 24, 24);
  font-weight: 600;
  padding: 13px;
  opacity: 0.8;
  white-space: nowrap;
}

.nav-btn:hover {
  opacity: 1;
}

.nav-btn:before {
  font-family: apple color emoji, segoe ui emoji, notocoloremoji,
    segoe ui symbol, android emoji, emojisymbols, emojione mozilla;
}

.menu-item:first-of-type a {
  border-radius: 13px 0 0 21px;
}

.menu-item:last-of-type a {
  border-radius: 0 21px 13px 0;
}

nav {
  max-width: 360px;
  max-height: 480px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-animation: bounceIn 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 1 forwards;
          animation: bounceIn 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 1 forwards;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  opacity: 0;
}

@-webkit-keyframes bounceIn {
  0% {
    opacity: 0;
    -webkit-transform: scaleX(0.55) scaleY(0.89);
            transform: scaleX(0.55) scaleY(0.89);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    -webkit-transform: scaleX(0.55) scaleY(0.89);
            transform: scaleX(0.55) scaleY(0.89);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-item {
  position: relative;
  display: -webkit-inline-flex;
  display: inline-flex;
}

@-webkit-keyframes showBounce {
  100% {
    opacity: 1;
    -webkit-transform: translateX(-21px) skewY(-5deg);
            transform: translateX(-21px) skewY(-5deg);
  }
}

@keyframes showBounce {
  100% {
    opacity: 1;
    -webkit-transform: translateX(-21px) skewY(-5deg);
            transform: translateX(-21px) skewY(-5deg);
  }
}

.menu-item a {
  position: relative;
  text-align: center;
}

.menu-item a:before {
  content: "";
  display: block;
  font-size: 34px;
  -webkit-transform: rotateZ(-8deg);
          transform: rotateZ(-8deg);
  margin-bottom: 5px;
  transition: -webkit-transform 189ms ease-out;
  transition: transform 189ms ease-out;
  transition: transform 189ms ease-out, -webkit-transform 189ms ease-out;
}

.menu-item a:hover:before {
  -webkit-animation: hoverEmoji 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
          animation: hoverEmoji 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
}

@-webkit-keyframes hoverEmoji {
  0% {
    -webkit-transform: scaleX(1.2) rotateX(-21deg) rotateZ(-8deg);
            transform: scaleX(1.2) rotateX(-21deg) rotateZ(-8deg);
  }
  100% {
    -webkit-transform: rotateZ(-8deg);
            transform: rotateZ(-8deg);
  }
}

@keyframes hoverEmoji {
  0% {
    -webkit-transform: scaleX(1.2) rotateX(-21deg) rotateZ(-8deg);
            transform: scaleX(1.2) rotateX(-21deg) rotateZ(-8deg);
  }
  100% {
    -webkit-transform: rotateZ(-8deg);
            transform: rotateZ(-8deg);
  }
}

nav > ol > .menu-item:nth-of-type(1) > a:before {
  content: "\1F3E0";
}

nav > ol > .menu-item:nth-of-type(2) > a:before {
  content: "\1F937";
}

nav > ol > .menu-item:nth-of-type(3) > a:before {
  content: "\1F39E";
}

nav > ol > .menu-item:nth-of-type(4) > a:before {
  content: "\1F5C2";
}

nav > ol > .menu-item:nth-of-type(5) > a:before {
  content: "\1F4E8";
}

nav > ol > li {
  width: 20%;
  margin-right: -4px;
}

